.club-cards {
  min-height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  h1 {
    max-width: 415px; }
  p {
    font-size: 30px;
    font-weight: normal;
    line-height: 1.33;
    letter-spacing: 1px;
    color: $white;
    max-width: 540px;
    margin-bottom: 200px;
    @include r(767) {
      font-size: 24px; } } }
