.saline-room {
  padding: 70px 0;
  padding-bottom: 100px;
  background: url('../img/shutterstock-657941563.png') no-repeat;
  background-size: cover;
  &__title {
    p {
      font-size: 30px;
      font-weight: normal;
      line-height: 1.33;
      letter-spacing: 1px;
      color: $white;
      margin-bottom: 55px;
      max-width: 735px; } }
  &__block {
    &__img {
      min-height: 150px;
      @include center;
      justify-content: flex-start; }
    p {
      font-size: 24px;
      font-weight: 600;
      line-height: 1.42;
      letter-spacing: 0.8px;
      color: $white;
      max-width: 215px; } } }
.feel {
  position: relative;
  padding-top: 100px;
  padding-bottom: 120px;
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 600px;
    height: 80px;
    background: $white;
    clip-path: polygon(0 0%, 0% 0, 100% 100%, 0 100%); }
  p {
    font-family: 'SF Pro Display';
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.42;
    letter-spacing: 1px;
    color: $pale-olive; } }
.features {
  background: $white;
  position: relative;
  padding: 120px 0;
  padding-bottom: 10px;
  &__block {
    margin-bottom: 140px;
    margin-top: 50px;
    min-height: 400px;
    h3 {
      font-size: 40px;
      font-weight: bold;
      letter-spacing: 0.5px;
      color: $charcoal;
      margin-bottom: 65px; }
    p {
      font-family: 'SF Pro Display';
      font-size: 24px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.42;
      letter-spacing: 1px;
      color: #333333;
      max-width: 440px; }
    &__img {
      max-width: 625px;
      position: absolute;
      overflow: hidden;
      height: 400px;
      border-radius: 5px;
      &-primary {
        left: -50px;
        border-bottom-right-radius: 90px;
        transform: skewX(-12deg);
        .features__block__img__content {
          width: 100%;
          height: 100%;
          transform: skewX(12deg); } }
      &-secondary {
        right: -50px;
        border-bottom-left-radius: 90px;
        transform: skewX(12deg);
        .features__block__img__content {
          width: 100%;
          height: 100%;
          transform: skewX(-12deg);
          margin-left: -50px; } }
      img {
       height: 100%; } } } }
